export const setPosts = (Posts) => {
  return {
    type: SET_POSTS,
    Posts,
  };
};

export const getPosts = (userId) => {
  return (dispatch) => {
    ProfileAPI.getAPIPosts(userId).then((response) => {
      dispatch(setPosts(response.data.posts));
    });
  };
};
export const sendPost = (userId, title, date) => {
  return (dispatch) => {
    ProfileAPI.sendAPIPost(userId, title, date, false, null);
  };
};

export const deletePost = (id, title) => {
  return (dispatch) => {
    ProfileAPI.deleteAPIPost(id, title);
  };
};
export const setLikes = (id, visLike, likes, title, date) => {
  return (dispatch) => {
    ProfileAPI.setAPILikes(id, visLike, likes, title, date);
  };
};
export const updateStatus = (userId, status) => {
  return (dispatch) => {
    ProfileAPI.updateAPIStatus(userId, status).then((response) => {
      dispatch(setStatus(status));
    });
  };
};
export const updateProfile = (userId, name, surName, gender, relationship, With, dateBirth, homeTown, languages) => {
  return (dispatch) => {
    ProfileAPI.updateAPIProfile(userId, name, surName, gender, relationship, With, dateBirth, homeTown, languages).then((response) => {
      dispatch(setName(name));
      dispatch(setsurName(surName));
      dispatch(setGender(gender));
      dispatch(setRelationship(relationship));
      dispatch(setWith(With));
      dispatch(setDateBirth(dateBirth));
      dispatch(setHomeTown(homeTown));
      dispatch(setLanguages(languages));
    });
  };
};

