import React from "react";
import "./Not_reveald_about_me_info.css";
import { useState } from "react";
import { useEffect } from "react";

const Not_reveald_about_me_info = (props) => {
  let [viewDate, setViewDate] = useState(true)
  function changeViewDate(){
    if (props.dateBirth.length < 1){
      setViewDate(false)
    } else {
      setViewDate(true)
    }
  }
  let [viewRel, setViewRel] = useState(true)
  function changeViewRel(){
    if (props.relationship.length < 1){
      setViewRel(false)
    } else {
      setViewRel(true)
    }
  }
  let [viewWith, setViewWith] = useState(true)
  function changeViewWith(){
    if (props.With.length < 1){
      setViewWith(false)
    } else {
      setViewWith(true)
    }
  }
  useEffect(() =>{changeViewDate(); changeViewRel(); changeViewWith()}, [props.dateBirth])
  return (
    <>
      <div className="date_profile_flex">
        <div className="span_mained_date_profile">
          {viewDate && <label className="span_fitst_about_mained" id="birh_1">
            День рождения:
          </label>}
          <label className="span_fitst_about_mained" id="birh_2">
            Город:
          </label>
          {viewRel && <label className="span_fitst_about_mained" id="birh_3">
            Семейное положение:
          </label>}
          {viewWith && <label className="span_fitst_about_mained" id="birh_3">
            С кем:
          </label>}
          <label className="span_fitst_about_mained" id="birh_4">
            Место учёбы:
          </label>
        </div>
        <div className="span_not_mained_date_profile">
          {viewDate && <div className="span_fitst_about_not_mained">
            <span>{props.dateBirth}</span>
          </div>}
          <div className="span_fitst_about_not_mained">
            <span>Тамбов</span>
          </div>
           {viewRel && <div className="span_fitst_about_not_mained">
            <span>{props.relationship}</span>
          </div>}
          {viewWith && <div className="span_fitst_about_not_mained">
            <span>{props.With}</span>
          </div>}
          <div className="span_fitst_about_not_mained">
            <span>Школа № 2</span>
          </div>
        </div>
      </div>
    </>
  );
};
export default Not_reveald_about_me_info;
