import React, { useState, createRef, useEffect } from "react";
import { getName } from "../../../store/profile-reduser";

import { connect } from "react-redux";
import Header_about_me from "./Header_about_me";
import { useContext } from "react";
import { AuthContext } from "../../../context/Auth-contex";

const Header_about_me_cont = (props) => {
  let auth = useContext(AuthContext)
 
  useEffect(() => {props.getName(auth.userId)}, []);
  return <Header_about_me id= {auth.userId}  name={props.name} />;
};

let mapStateToProps = (state) => {
  return {
    name: state.Profile.name,
  };
};

let mapDispatchToProps = {
  getName
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Header_about_me_cont);
