import React, { useEffect } from "react";
import "./Profile_photo.css";
import { useState } from "react";
import * as axios from "axios";
import { NavLink } from "react-router-dom";
import { set } from "mongoose";

const Profile_photo = (props) => {
  let [selectedFile, setSelectedFile] = useState(null);
  let [src, setSrc] = useState("");
  let [visCh, setVisCh] = useState(true);

  let fileSelectorHandler = async (event) => {
    setSelectedFile(event.target.files[0]);
  };

  let [photos, setPhotos] = useState(props.photoId);
  async function k() {
    let photId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`
    );
    setPhotos(photId.data.photo);
  }

  function fff() {
    if (visCh == false) {
      setVisCh(true);
    }
    if (visCh == true) {
      setVisCh(false);
    }
  }
  async function dsf() {
    const id = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/photo`
    );

    axios.put(
      `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
      { photo: id.data[0]._id }
    );
  }
  async function fd(e){
    axios.put(
      `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
      { photed: true }
    );
  }

  async function fds(e) {
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.id}`
    );
    
    const id = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/photo`
    );
    
      console.log(photoId.data.photed)
    if (photoId.data.photo == "") {
      setSrc("https://vk.com/images/camera_100.png?ava=1");
      if (photoId.data.photed == true){
        axios.put(
          `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
          { photo: id.data[0]._id }
        );
        console.log(photoId.data.photed)
        axios.put(
          `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
          { photed: false }
        );
      }
      console.log(photoId.data.photed)
    } else {
      if (photoId.data.photed == true){
        axios.put(
          `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
          { photo: id.data[0]._id }
        );
        console.log(photoId.data.photed)
        axios.put(
          `https://dry-headland-66248.herokuapp.com/api/photos/${props.id}`,
          { photed: false }
        );
      }
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then(async (response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });

      
    }
  }

  useEffect(() => {
    fds();
  }, [src]);

  return (
    <div className="profile_photo">
      <img
        onClick={fff}
        src={src || "https://vk.com/images/camera_100.png?ava=1"}
        alt=""
        className="profile_photo_img"
      />

      <div className="flex_profile_photo">
        {!visCh && (
          <form
            className="acd"
            action="https://dry-headland-66248.herokuapp.com/api/upload"
            method="POST"
            encType="multipart/form-data"
          >
            <input onChange={fileSelectorHandler} type="file" name="file" />

            <button onClick ={fd}>Загрузите фотогрфаию</button>
          </form>
        )}
        <NavLink className="per_to_edit" to="edit">
          <button className="bt1_photo">Редактировать</button>
        </NavLink>
        <button className="bt2_photo">...</button>
      </div>
    </div>
  );
};
export default Profile_photo;
