import React, { useState } from "react";
import "./Profile_photos_wall.css";
import { useEffect } from "react";
import * as axios from "axios";

const Profile_photos_wall = (props) => {
  let [src, setSrc] = useState('')
  async function fds(e) {
    let photoId = await axios.get(
      `https://dry-headland-66248.herokuapp.com/api/Ttatus/${props.userId}`
    );
   
    
    if (photoId.data.photo == "") {
      setSrc("https://vk.com/images/camera_100.png?ava=1");
    } else {
      axios
        .get(
          `https://dry-headland-66248.herokuapp.com/api/upload/${photoId.data.photo}`
        )
        .then((response) => {
          setSrc("data:image/jpeg;base64," + response.data.data);
        });
    }
   
  }
  useEffect(() =>{fds()}, [])
  return (
    <div className = 'wrapper_photos_profile_wall'>
        <div className="text_photos_profile_wall">
            <h1>Мои</h1> <h1>фотографии</h1>
            <span>1</span>
        </div>
        <div className="photos_photos_profile_wall">
            <img src={src} alt=""/>
        </div>
    </div>
  );
};

export default Profile_photos_wall;
