import React, { useState, useContext } from "react";

import {getGender, getRelationship, getWith, getDateBirth, getHomeTown, getLanguages} from '../../../../../store/profile-reduser'

import About_me_info from "./About_me_info";

import * as axios from "axios";
import { connect } from "react-redux";
import { AuthContext } from "../../../../../context/Auth-contex";
import { useEffect } from "react";





const  About_me_cont = (props) => {
  let auth = useContext(AuthContext)
  useEffect(() =>{ props.getGender(auth.userId); props.getRelationship(auth.userId); props.getWith(auth.userId); props.getDateBirth(auth.userId); props.getHomeTown(auth.userId); props.getLanguages(auth.userId) }, [])
  
   
  return <About_me_info gender = {props.gender} relationship ={props.relationship} With = {props.With} dateBirth ={props.dateBirth} homeTown = {props.homeTown} languages= {props.languages}/>;

}
let mapStateToProps = (state) =>{
 return{
    gender: state.Profile.gender,
  relationship: state.Profile.relationship,
  With: state.Profile.With,
  dateBirth: state.Profile.dateBirth,
  homeTown: state.Profile.homeTown,
  languages: state.Profile.languages,
}
}
let mapDispatchToProps = {
    getGender,
    getRelationship,
    getWith,
    getDateBirth,
    getHomeTown,
    getLanguages
}

export default connect(mapStateToProps, mapDispatchToProps)(About_me_cont);
