import React from "react";
import "./Edit_page.css";
import Header_edit_page from "./header_edit_page/Header_edit_page";
import { useState } from "react";

const Edit_page = (props) => {
  let [view, setView] = useState(false);

  let [names, setName] = useState(props.name);
  let [surNames, setSurNames] = useState(props.surName);
  let [genders, setGenders] = useState(props.gender);
  let [date, setDate] = useState(props.dateBirth);
  let [relationships, setRelationships] = useState(props.relationship);
  let [withs, setWiths] = useState(props.With);
  let [homeTowns, setHomeTown] = useState(props.homeTown);
  let [lang, setLang] = useState(props.languages);

  let refName = React.createRef();
  let refSurName = React.createRef();
  let refGender = React.createRef();
  let refRel = React.createRef();
  let refWith = React.createRef();
  let refDate = React.createRef();
  let refHome = React.createRef();
  let refLang = React.createRef();


  function changeView() {
    if (view == false) {
      setView(true);
    }
  }

  function changeHandlerName(e) {
    setName(e.currentTarget.value);
  }
  function changeHandlerSurName(e) {
    setSurNames(e.currentTarget.value);
  }
  function changeHandlerGender(e) {
    setGenders(e.currentTarget.value);
  }
  function changeHandlerRel(e) {
    setRelationships(e.currentTarget.value);
  }
  function changeHandlerDate(e) {
    setDate(e.currentTarget.value);
  }
  function changeHandlerWith(e) {
    setWiths(e.currentTarget.value);
  }
  function changeHandlerHome(e) {
    setHomeTown(e.currentTarget.value);
  }
  function changeHandlerLang(e) {
    setLang(e.currentTarget.value);
  }

  function updProfile() {
    props.updateProfile(
      props.id,
      refName.current.value,
      refSurName.current.value,
      refGender.current.value,
      refRel.current.value,
      refWith.current.value,
      refDate.current.value,
      refHome.current.value,
      refLang.current.value,
    );
  }

  return (
    <div className="wrapper_edit_page">
      <Header_edit_page />
      {view && (
        <div className="wrapper_saved_edit">
          <img src="https://vk.com/images/icons/msg_ok.png?1" alt="" />
          <div className="text_saved_edit">
            <span className="sp_changes_saved">Изменения сохранены.</span>
            <span className="sp_changes_will_see">
              Новые данные будут отражены на Вашей странице.
            </span>
          </div>
        </div>
      )}
      <div className="flex_input_edit">
        <label for="input">Имя:</label>
        <input
          ref={refName}
          type="text"
          onChange={changeHandlerName}
          value={names}
        />
      </div>
      <div className="flex_input_edit">
        <label>Фамилия:</label>
        <input
          ref={refSurName}
          onChange={changeHandlerSurName}
          type="text"
          value={surNames}
        />
      </div>
      <div className="flex_input_edit">
        <label>Пол:</label>
        <select onChange={changeHandlerGender} ref={refGender}>
          <option>мужской</option>
          <option>женский</option>
        </select>
      </div>
      <div className="flex_input_edit">
        <label>Семейное положение:</label>
        <select onChange={changeHandlerRel} ref={refRel} value = {relationships} type="selector">
          <option value ='Не выбрано'>Не выбрано</option>
          <option value ='Не женат'>Не женат</option>
          <option value ='Встречаюсь'>Встречаюсь</option>
          <option value ='Помолвлен'>Помолвлен</option>
          <option value ='Женат'>Женат</option>
          <option value ='В гражданском брак'>В гражданском браке</option>
          <option value ='Влюблён'>Влюблён</option>
          <option value ='Всё сложно'>Всё сложно</option>
          <option value ='В активном поиске'>В активном поиске</option>
        </select>
      </div>
      <div className="flex_input_edit">
        <label>С кем:</label>
        <select onChange={changeHandlerWith} ref={refWith}>
          <option value="">Ни с кем</option>
        </select>
      </div>
      <div className="flex_input_edit">
        <label>День рождения:</label>
        <input
          onChange={changeHandlerDate}
          ref={refDate}
          type = 'date'
          value ={date}
        ></input>
      </div>
      <div className="flex_input_edit">
        <label>Родной город:</label>
        <input
          onChange={changeHandlerHome}
          ref={refHome}
          type="selector"
          value={homeTowns}
        />
      </div>
      <div className="flex_input_edit">
        <label>Языки:</label>
        <input
          onChange={changeHandlerLang}
          ref={refLang}
          value={lang}
          type="selector"
        ></input>
      </div>
      <div id="polosa_edit" class="polosa_bottom_about_me"></div>
      <div onClick={changeView}>
        <button onClick={updProfile} className="bt_save_edit">
          Сохранить
        </button>
      </div>
    </div>
  );
};

export default Edit_page;
