import React, { useState, useContext } from "react";
import {
  getName,
  getsurName,
  getGender,
  getDateBirth,
  getHomeTown,
  getLanguages,
  getRelationship,
  getWith,
  updateProfile,
} from "../../store/profile-reduser";

import { connect } from "react-redux";
import { AuthContext } from "../../context/Auth-contex";
import { useEffect } from "react";
import Edit_page from "./Edit_page";

const Edit_page_const = (props) => {
  let auth = useContext(AuthContext);

  return (
    <Edit_page
      gender={props.gender}
      relationship={props.relationship}
      With={props.With}
      dateBirth={props.dateBirth}
      homeTown={props.homeTown}
      languages={props.languages}
      id={auth.userId}
      updateProfile={props.updateProfile}
      surName={props.surName}
      name={props.name}
    />
  );
};
let mapStateToProps = (state) => {
  return {
    name: state.Profile.name,
    surName: state.Profile.surName,
    gender: state.Profile.gender,
    relationship: state.Profile.relationship,
    With: state.Profile.With,
    dateBirth: state.Profile.dateBirth,
    homeTown: state.Profile.homeTown,
    languages: state.Profile.languages,
  };
};
let mapDispatchToProps = {
  getName,
  getsurName,
  updateProfile,
  getGender,
  getDateBirth,
  getHomeTown,
  getLanguages,
  getRelationship,
  getWith,
};

export default connect(mapStateToProps, mapDispatchToProps)(Edit_page_const);
