import React from "react";
import './Profile_about.css'

import Profile_about_page from "./profile_about_page/Profile_about_page";

import Profile_about_top_cont from "./profile_about_top/profile_about_top_cont";
import About_me_cont from "./about_me_info/About_me_cont";


const Profile_about = (props) => {
  return (
    <div className="profile_about">
      <Profile_about_top_cont userId = {props.userId}/>
      <About_me_cont />
      <div className="polosa_bottom_about_me"></div>
      <Profile_about_page />
    </div>
  );
};

export default Profile_about
